.selection__wrapper {
  padding: 20px 0 20px 0;
  border-top: solid 1px $border-gray;
}

@media (min-width: $tablet-width) {
  .selection__wrapper {
    border-top: none;
  }
}

.selection__inner {
  padding: 0 20px 0 20px;
}

@media (min-width: $tablet-width) {
  .selection__inner {
    padding: 0 70px 0 70px;
  }
}

.selection__title {
  font-size: 36px;
  line-height: 36px;
  margin: 0 0 37px 0;
  font-weight: 400;
}

@media (min-width: $tablet-width) {
  .selection__title {
    font-size: 60px;
    font-weight: 400;
  }
}

.selection__description {
  text-transform: uppercase;
  margin: 0;
}

@media (min-width: $tablet-width) {
  .selection__description {
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: $desktop-width) {
  .fields__set-container {
    display: flex;
  }
}

.selection__form {
  padding: 0 20px 0 20px;
}

@media (min-width: $tablet-width) {
  .selection__form {
    padding: 0 70px 0 70px;
  }
}

.fields__set {
  border: 0;
  padding: 0;
  margin: 0;
}

.fields__set-personal {
  padding: 20px 0 24px 0;
}

@media (min-width: $desktop-width) {
  .fields__set-personal {
    margin-right: 80px;
  }
}


@media (min-width: $desktop-width) {
  .fields__set-personal .fields__text-field {
    min-width: 510px;
  }
}

.fields__set-personal,
.fields__set-contacts {
  .fields__item:not(:last-child) {
    margin-bottom: 14px;
  }
}

@media (min-width: $desktop-width) {
  .fields__set-personal .fields__item:not(:last-child) {
    margin-bottom: 20px;
  }
}

.fields__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.fields__label {
  display: flex;
  margin-bottom: 14px;
  align-items: center;
}

.fields__label:last-child {
  margin-bottom: 0;
}

.fields__title {
  margin-right: 13px;
  font-size: 16px;
  text-transform: uppercase;
  white-space: nowrap;
  color: $dark-gray;
}

@media (min-width: $tablet-width) {
  .fields__title {
    font-size: 20px;
  }
}

.fields__text-field {
  font-size: 16px;
  font-family: "Oswald", sans-serif;
  border: 1px solid #e7e7e7;
  text-transform: uppercase;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  min-height: 45px;
}

@media (min-width: $tablet-width) {
  .fields__text-field {
    font-size: 20px;
  }
}

.fields__set-program {
  margin-right: -20px;
  margin-left: -20px;
  padding: 22px 20px 19px 20px;
  border-top: 1px solid #e7e7e7;
  border-bottom: 1px solid #e7e7e7;
}

@media (min-width: $tablet-width) {
  .fields__set-program {
    border: 1px solid #e7e7e7;
  }
}

@media (min-width: $tablet-width) {
  .fields__set-program--wrapper {
    border: none;
    padding: 52px 20px 52px 20px;
  }
}

@media (min-width: $desktop-width) {
  .fields__set-program--wrapper {
    border: none;
    padding: 20px;
    width: 100%;
  }
}

.fields__set-program .fields__label {
  margin: 0 0 23px 0;
  padding: 0 0 0 50px;
  position: relative;
  display: block;
}

.fields__set-program .fields__label-mark {
  position: absolute;
  top: -5px;
  left: 0;
  width: 30px;
  height: 30px;
  border: 2px solid #cdcdcd;
  border-radius: 100%;
}

.fields__set-program .fields__text-field[type="radio"]:checked + .fields__label-mark::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 6px;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  background-color: #68b738;
  border-radius: 50%;
}

.fields__set-program .fields__text-field {
  padding: 0;
  margin: 0 15px 0 0;
  width: 30px;
  height: 30px;
}

.fields__set-contacts {
  padding: 26px 20px 10px 0;
  position: relative;
  margin-right: -20px;

  &::after {
    content: "";
    z-index: -1;
    width: 100%;
    height: 2px;
    position: absolute;
    top: -35px;
    right: 0;
    background-color: #68b738;
  }
}

@media (min-width: $tablet-width) {
  .fields__set-contacts {
    padding: 26px 20px 10px 50px;
    position: relative;
    margin-right: -70px;
    margin-left: -70px;

    &::after {
      top: -15px;
      right: 0;
    }
  }
}

.fields__set-contacts legend {
  padding: 0;
}

.fields__set-contacts h2 {
  background-color: $basic-white;
  padding-right: 20px;
}

@media (min-width: $tablet-width) {
  .fields__set-contacts h2 {
    padding-left: 20px;
  }
}

@media (min-width: $desktop-width) {
  .fields__set-contacts .fields__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

@media (min-width: $desktop-width) {
  .fields__set-contacts .fields__text-field {
    min-width: 494px;
  }
}

.fields__item--contacts .fields__label {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    top: 50%;
    right: 12px;
    transform: translateY(-50%);
    background-image: url("../img/form/email.svg");
    background-repeat: no-repeat;
    background-position: center;
    z-index: 1;
  }
}

.fields__item--contacts .fields__label--phone::before {
  background-image: url("../img/form/phone.svg");
}

.fields__text-field--email,
.fields__text-field--phone {
  position: relative;
  padding: 0 35px 0 15px;
}

.fields__set h2 {
  margin: 0;
  padding-top: 26px;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
}

.fields__set h2 span {
  display: block;
}

@media (min-width: $tablet-width) {
  .fields__set h2 span {
    display: inline-block;
  }
}

@media (min-width: $tablet-width) {
  .fields__set h2 {
    font-size: 36px;
    line-height: 36px;
  }
}

.fields__set-comments {
  padding: 26px 20px 10px 0;
  position: relative;
  margin-right: -20px;

  &::after {
    content: "";
    z-index: -1;
    width: 100%;
    height: 2px;
    position: absolute;
    top: -47px;
    right: 0;
    background-color: #68b738;
  }
}

@media (min-width: $tablet-width) {
  .fields__set-comments {
    padding: 26px 20px 10px 50px;
    position: relative;
    margin-right: -70px;
    margin-left: -70px;

    &::after {
      top: -50px;
      right: 0;
    }
  }
}

.fields__set-comments legend {
  padding: 0;
}

.fields__set-comments h2 {
  background-color: $basic-white;
  padding-right: 20px;
  padding-top: 24px;
  margin-bottom: 38px;
}

@media (min-width: $tablet-width) {
  .fields__set-comments h2 {
    padding-left: 20px;
  }
}

.fields__set-comments .fields__label {
  margin: 0;
}

.fields__text-area {
  padding: 14px;
  width: 100%;
  min-height: 150px;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.fields__set-additionally {
  padding: 26px 20px 10px 0;
  position: relative;
  margin-right: -20px;

  &::after {
    content: "";
    z-index: -1;
    width: 100%;
    height: 2px;
    position: absolute;
    top: -47px;
    right: 0;
    background-color: #68b738;
  }
}

@media (min-width: $tablet-width) {
  .fields__set-additionally {
    padding: 26px 20px 10px 50px;
    position: relative;
    margin-right: -70px;
    margin-left: -70px;

    &::after {
      top: -50px;
      right: 0;
    }
  }
}

.fields__set-additionally legend {
  padding: 0;
}

//

.fields__set-additionally .fields__label {
  margin: 0 0 23px 0;
  padding: 0 0 0 50px;
  position: relative;
  display: block;
}

.fields__set-additionally .fields__label-mark {
  position: absolute;
  top: -5px;
  left: 0;
  width: 30px;
  height: 30px;
  border: 2px solid #cdcdcd;
  border-radius: 0;
}

.fields__set-additionally .fields__text-field[type="checkbox"]:checked + .fields__label-mark::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 26px;
  height: 26px;
  background-image: url("../img/index/tick.svg");
  background-size: contain;
  background-position: center;
}

//

.fields__set-additionally h2 {
  background-color: $basic-white;
  padding-right: 20px;
  padding-top: 23px;
  margin-bottom: 36px;
}

@media (min-width: $tablet-width) {
  .fields__set-additionally h2 {
    padding-left: 20px;
  }
}

@media (min-width: $tablet-width) {
  .fields__set-additionally .fields__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

@media (min-width: $tablet-width) {
  .fields__set-additionally .fields__item {
    min-width: 220px;
  }
}

.fields__set-additionally .fields__text-field {
  padding: 0;
  margin: 0 15px 0 0;
  width: 30px;
  height: 30px;
}

.fields__button-container {
  padding: 17px 0 32px 0;
  text-align: center;
}

.fields__button {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  background-color: $basic-green;
  color: $basic-white;
  padding: 10px 20px;
  margin-bottom: 21px;
  border: 0;
  cursor: pointer;
  white-space: nowrap;
  min-width: 280px;
}

@media (min-width: $tablet-width) {
  .fields__button {
    font-size: 20px;
    min-width: 282px;
  }
}

.fields__note {
  display: block;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 16px;
}

@media (min-width: $tablet-width) {
  .fields__note {
    font-size: 16px;
  }
}

.cooperation__wrapper--secondary {
  background-color: #f2f2f2;
}

@media (min-width: $tablet-width) {
  .fields__button-container {
    display: flex;
    align-items: center;
  }
}

@media (min-width: $tablet-width) {
  .fields__button {
    margin-right: 64px;
    margin-bottom: 0;
  }
}
